.pagination {
    list-style-type: none;
    display: flex;
    justify-content: center;
    margin-top: 20px;
}

.pagination li {
    cursor: pointer;
    margin: 0 5px;
    padding: 5px 10px;
    border: 1px solid #ccc;
    border-radius: 5px;
}

.pagination li.active {
    background-color: #0c802f;
    color: #fff;
}


/* Table styling */
table {
    width: 100%;
    border-collapse: collapse;
    margin-bottom: 20px;
}

thead th {
    background-color: #f2f2f2;
    color: #333;
    font-weight: bold;
    padding: 8px;
}

tbody td {
    border: 1px solid #ddd;
    padding: 8px;
}

/* Indicator signal styling */
tbody .signal {
    color: #020202;
    /* Change color as needed */
    font-weight: bold;
}

/* Time styling */
tbody .time {
    color: #333;
    /* Change color as needed */
}